canvas {
  position: absolute;
  top: 10px;
  left: 10px;
  z-index: 0;
}

.ui {
  display: -webkit-flex;
  display: flex;
  position: absolute;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex: 1 1;
          flex: 1 1;
  -webkit-align-items: center;
          align-items: center;
  pointer-events: none;
  width: 100%;
  height: 100%;
}

.ui .infos {
  background-color: rgba(10, 10, 10, 0.6);
  color: white;
  width: 100%;
  text-align: center;
  padding: 2em;
}

.ui .infos h1,
.ui .infos h3 {
  padding: 0;
  margin: 0;
}

.ui .actions {
  margin-top: auto;
  margin-left: auto;
}

.ui .actions button {
  padding: 1em;
  margin: 1em;
  pointer-events: auto;
}

